body {
    font-family: $fontMontserrat;
    font-size: $baseFontSize;
    color: $textColorGrey;
    background-color: $backGroundBeige;
}

.container {
    width: 100%;

    padding-right: 15px;
    padding-left: 15px;

    margin-right: auto;
    margin-left: auto;

    @include mobile {
        max-width: 414px;
    }

    @include tablet {
        max-width: 1024px;

        padding-right: 47px;
        padding-left: 47px;
    }

    @include desktop {
        max-width: 1280px;

        padding-right: 85px;
        padding-left: 85px;
    }
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}